@font-face {
	font-family: 'BlackswordVU-Regular';
	src: url('fonts/BlackswordVU-Regular.eot');
	src: local('☺'), url('fonts/BlackswordVU-Regular.woff') format('woff'), url('fonts/BlackswordVU-Regular.ttf') format('truetype'), url('fonts/BlackswordVU-Regular.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}
@font-face {
	font-family: 'CenturySchlBkVU-Bold';
	src: url('fonts/CenturySchlBkVU-Bold.eot');
	src: local('☺'), url('fonts/CenturySchlBkVU-Bold.woff') format('woff'), url('fonts/CenturySchlBkVU-Bold.ttf') format('truetype'), url('fonts/CenturySchlBkVU-Bold.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}
@font-face {
	font-family: 'GillSansVU-Regular';
	src: url('fonts/GillSansVU-Regular.eot');
	src: local('☺'), url('fonts/GillSansVU-Regular.woff') format('woff'), url('fonts/GillSansVU-Regular.ttf') format('truetype'), url('fonts/GillSansVU-Regular.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}
@font-face {
	font-family: 'GillSansVU-SemiBold';
	src: url('fonts/GillSansVU-SemiBold.eot');
	src: local('☺'), url('fonts/GillSansVU-SemiBold.woff') format('woff'), url('fonts/GillSansVU-SemiBold.ttf') format('truetype'), url('fonts/GillSansVU-SemiBold.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}
@color1 : #6BA31E;
@color2 : #00589d;
body{
	font-family: 'GillSansVU-Regular';
}
.navbar-dark {
	.navbar-nav {
		.nav-link {
			color: #111;
			font-size: 1rem;
			padding-right: 1.5rem;
			padding-left: 1.5rem;
			&:hover {
				color: @color1;
			}
		}
		.nav-link.active {
			color: #111;
		}
	}
}
img{
	max-width: 100%;
	height: auto;
}
.pdl-30 {
	padding-left: 30px;
}
.pdl-100 {
	padding-left: 100px;
}
.mrl-50 {
	margin-left: 50px;
}
#navbarNavAltMarkup {
	justify-content: center;
}
.container{
	max-width: 1140px;
}
ul{
	margin-bottom: 0px;
}
body.show-menu {
	.navbar {
		background: #fff;
	}
}
#banner_top{
	padding-top: 150px;
	padding-bottom: 50px;
	.md-banner{
		margin: 0 auto;
		max-width: 1200px;
		img{
			max-width: 100%;
		}
	}
}

.navbar.in {
	.ts-background {
		background: #fff !important;
		border-bottom: 1px solid #cdcdcd;
	}
}
.copy-right {
	padding-top: 50px;
	padding-bottom: 50px;
	small {
		font-size: 16px;
	}
}
.ts-title {
	h2 {
		font-size: 3rem;
	}
}
#tongquan{
	padding:100px 0 170px;
	.content-tongquan{
		position: relative;
		.left{
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		.content-tq{
			position: relative;
			.title{
				position: absolute;
			    top: 40px;
			    left: 8%;
			    font-weight: 500;
			    text-transform: uppercase;
			}
			.list-content{
				position: absolute;
				bottom:50px;
				max-width: 450px;
			    width: 100%;
			    left: 8%;
			    ul{
			    	li{
			    		font-size:16px;
			    		line-height: 28px;
			    		padding-left:20px;
			    		position:relative;
			    		&:before{
			    			position: absolute;
						    width: 10px;
						    height: 10px;
						    top: 9px;
						    left: 0px;
						    content: "";
						    background: #000;
						    border-radius: 50%;
			    		}
			    	}
			    }
			}
		}
		}
		.right{
			text-align: right;
		}
	}
	

}
#vitri{
	padding: 20px 0 130px;
	.content-vitri{
		    display: flex;
    		align-items: self-end;
		.left{
			float: left;
			width:40%;
			position:relative;
			background: #e8f6f1;
			padding-top:30px;
			overflow: hidden;
			margin-top: -150px;
			.bgr{
				text-align: center;
			}
			.title{
				position: relative;
				z-index: 1;
				&:before{
					position: absolute;
					content: "";
					left: 15%;
					bottom: 10px;
					height: 1px;
					width: 85%;
					background:#00589d;
					z-index: -1;
				}
				h3{
					max-width: 430px;
					width:100%;
					margin: 0 auto;
					font-size: 40px;
					line-height: 42px;
					color:@color2;
					text-transform: uppercase;
					margin-bottom: 20px;
					span{
						padding-right: 40px;
    					background: #e8f6f1;
    					position: relative;
    					&:before{
							position: absolute;
						    content: "";
						    right: -10px;
						    bottom: 8px;
						    height: 15px;
						    width: 15px;
						    background: @color2;
						    border-radius: 50%;
						}
    				}
				}
			}
			.content-tq{
				.list-content{
					max-width: 430px;
					width:100%;
					margin: 0 auto;
				}
				
			}
		}
		.right{
			float: right;
			width:60%;
			text-align: center;
		}
	}
	
}

#matbang_tongthe{
	.content-matbang{
		text-align:center;
		.title{
			margin-bottom: -20px;
			text-align:center;
			position: relative;
			z-index: 2;
		}
		.slide-matbang{
			position: relative;
			z-index: 1;
		}
	}
}
.tienich{
	margin-top: -100px;
	padding-bottom: 100px;
	.container{
		
		.grid{
			column-count: 3;
			column-gap: 10px;
			
			.grid-item{
			margin: 0;
			display: grid;
			grid-template-rows: 1fr auto;
			margin-bottom: 10px;
			break-inside: avoid;
			position: relative;
			&.title{
				padding-top: 160px;
			}
				img{
					grid-row: 1 / -1;
					grid-column: 1;
				}
				.content-title{
					position: absolute;
					bottom: 0px;
					left: 0px;
					background: ;
					.content-text{
						padding: 0 15px 0 40px;
						position: relative;
						&:after{
							position: absolute;
							left: 25px;
							top:5px;
							height: 90%;
							width: 5px;
							border-radius: 5px;
							background: #fff;
							content: '';
						}
					}
					
					h3{
						font-size: 20px;
						color: #fff;
						font-family: 'Gill SansVU';
						margin-bottom: 15px;
					}
					.content-detail{
						p{
							color: #fff;
							font-size: 16px;
							line-height: 24px;
						}
					}
				}
			}
		}
	}
}


#matbang_canho{
	background: #cdcece;
	padding: 70px 0;
	.title{
			margin-bottom: -40px;
			text-align:center;
			position: relative;
			z-index: 2;
	}
	ul{
		li{
			.caption-slide{
				text-align:center;
				color: #fff;
				font-size:20px;
				line-height:24px;
				font-family: 'Gill SansVU';
				padding-top:20px;
			}
		}
	}
}
#thuvien{
	padding: 90px 0;
	.title{
			margin-bottom: -40px;
			text-align:center;
			position: relative;
			z-index: 2;
	}
	.slide-thumb{
		margin-top: -110px;
	}
}
#chinhsach_banhang{
	padding:90px 0;
	
	.title{
		    margin-top: 0px;
	    text-align: center;
	    top: -40px;
	    z-index: 2;
	    width: 100%;
	    left: 0;
	    position: absolute;
	}
	.list-chinhsach{
		max-width:700px;
			width:100%;
			margin:0 auto;
		ul{
			margin-left: -15px;
			margin-right: -15px;
			overflow: hidden;
			li{
				width: 33.33%;
				padding-left:15px;
				padding-right:15px;
				float: left;
				padding-bottom:10px;
				.content-cs{
					text-align:center;
					min-height: 260px;
					background:#fff;
					padding-top:15px;
					.thumbnail{
						
					}
					.caption-slide{
						padding:20px 10px 0;
						h5{
							font-size: 18px;
							line-height: 20px;
							color: #373737;
							font-weight: 400;
							span{
								font-size: 14px;
								display: block;
							}
						}
					}
				}
			}
		}
	}
}
.dangky{
	padding: 90px 0;
	text-align:center;
}
.lienhe{
	padding-top: 50px;
	.content-lienhe{
		display: flex;
		.left{
			width: 50%;
			float: left;
			h3{
				font-size: 40px;
				line-height: 40px;
				font-family: 'Gill SansVU';
				font-weight: 600;
				margin-bottom: 15px;
			}
			.text-detail{
				font-size: 16px;
				color: #616161;
				a{
					color: #18a574;
				}
			}
		}
		.right{
			width: 50%;
			float: right;
		}
	}
	
}
#contact{
	padding: 20px 0;
	.content-contact{
		display: flex;
		align-items: center;
		.left{
			width: 20%;
			text-align: center;
		}
		.center{
			width: 60%;
			text-align: center;
			position: relative;
			ul{
				position: absolute;
				bottom: 30px;
				max-width: 410px;
				width: 100%;
				margin: 0 auto;
				left: 50%;
				transform: translateX(-50%);
				text-align: left;
				
				li{
					position: relative;
					padding-left: 40px;
					p{
						font-size: 16px;
						line-height: 24px;
						margin-bottom: 10px;
					}
					i{
						position: absolute;
						left: 0;
						font-size: 16px;
						top: 0;
						line-height: 16px;
					}
				}
			}
		}
		.right{
			width: 20%;
			text-align: center;
		}
	}
}
.social{
	padding: 15px 0;
	background: #000;
	text-align: center;
	a{
		width: 40px;
		height: 40px;
		border-radius: 50%;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		background: #fff;
		margin-left: 5px;
		margin-right: 5px;
		i{
			font-size: 20px;
			color: #0e7f8b;
			line-height: 20px;
		}
	}
}
.logo-mobile{
	display: none;
}
@media (max-width:768px) {
	.logo-mobile{
		display: block;
	}
	.navbar {
		.ts-background {
			background: transparent !important;
		}
	}
	.navbar.in {
		.ts-background {
			background: #fff !important;
			border-bottom: 1px solid #cdcdcd;
		}
	}
	#about-us {
		.button-about {
			padding-top: 10px;
			padding-bottom: 10px;
		}
	}
	#tongquan {
		padding: 40px 0;
		.content-tongquan .left{
			position: relative;
			top:0;
			transform: inherit;
			display: flex;
			justify-content: center;
		}

	}
	#vitri{
		.content-vitri{
			.left{
				width: 100%;
				margin-top: 0px;
				.title{
					h3{
						font-size: 30px;
						span:before{
							bottom: 0px;
						}
						padding: 0 10px;
					}
					
				}
				.list-content{
					padding: 0 10px;
				}
			}
			.right{
				width: 100%;
			}
		}
	}
	.tienich{
		margin-top: -50px;
		.container .grid .grid-item.title {
			padding-top: 105px;
		}
	}

}


@media (max-width:480px){
	#tongquan .content-tongquan .left .content-tq .list-content ul li{
		font-size: 14px;
    	line-height: 24px;
	}
	#vitri .content-vitri{
		display: block;
		overflow: hidden;
	}
	.tienich .container .grid .grid-item.title{
		padding-top:0px;
	}
	.tienich .container .grid{
		columns: 1;
	}
	#matbang_tongthe .content-matbang .title{
		max-width: 80%;
		margin: 0 auto;
		margin-bottom: -10px;
	}
	#matbang_canho {
		padding: 30px 0;
		.title{
			max-width: 80%;
			margin: 0 auto;
		}
	}
	#dangky{
		.title{
			max-width: 80%;
			margin: 0 auto;
		}
	}
	#thuvien{
		padding: 40px 0;
		.title{
			max-width: 80%;
			margin: 0 auto;
		}

	} 
	#chinhsach_banhang{
		padding: 30px 0;
		.title{
			position: relative;
			top:0px;
		}
		.list-chinhsach ul li{
			width: 50%;
			padding-left: 10px;
			padding-right: 10px;
			.content-cs{
				min-height: 240px;
				.caption-slide{
					padding-top: 10px;
					h5{
						font-size: 14px;
					}
				}
			} 
		}
	}
	.lienhe .content-lienhe{
		display: block;
	}
	.lienhe .content-lienhe .left{
		width: 100%;
	}
	.lienhe .content-lienhe .right{
		width: 100%;
	}
	#contact .content-contact{
		display: block;
	}
	#contact .content-contact .left{
		width: 100%;
	}
	#contact .content-contact .center{
		width: 100%;
		padding-top: 20px;
	}
	#contact .content-contact .right{
		width: 100%;
		padding-top: 20px;
	}
	#contact .content-contact .center ul{
		max-width: 330px;
		bottom: 0px;
		li p{
			font-size: 14px;
		}
	}
}